<template>
    <div class="music-drive">
      <div class="search-box">
        <h2 class="text-center">Tìm bài hát</h2>
        <b-input-group size="lg" prepend="Tên bài hát">
          <b-form-input v-model="songKey" type="text"></b-form-input>
          <b-input-group-append>
            <b-btn variant="info" @click="findSong">Button</b-btn>
          </b-input-group-append>
        </b-input-group>
        <p>Value: {{ songKey }}</p>
      </div>

      <div class="row">
        <div class="col-4">
          <b-list-group id="search-result" v-for="song in songs">
            <b-list-group-item href="#some-link">{{ song.songName }}</b-list-group-item>
          </b-list-group>
        </div>
        <div class="col-4"></div>
        <div class="col-4"></div>
      </div>
    </div>
</template>

<script>
import {AXIOS} from './http-common'

export default {
  name: 'MusicDrive',
  data () {
    return {
      response: [],
      songKey: '',
      errors: [],
      songs: [
        {}
      ]
    }
  },
  methods: {
    findSong () {
      AXIOS.get(`/` + this.songKey)
        .then(response => {
          this.songs = response.data
          console.log(response.data)
        })
        .catch(e => {
          this.errors.push(e)
        })
    }
  }
}
</script>

<style scoped>
.row {
  margin-top: 50px;
}
</style>



